
import { defineComponent } from "vue";
import { useStore, mapState } from "vuex";
import { key } from "@/store/store";
import SvgIcon from "./SvgIcon.vue";
import HotspotPage from "@/models/HotspotPage";

export default defineComponent({
  name: "SmallHotspotPageView",
  components: {
    SvgIcon
  },
  props: {
    page: {
      type: HotspotPage,
      required: true
    }
  },
  data () {
    return {
      store: useStore(key)
    };
  },
  computed: {
    ...mapState([
      "sessionConfig"
    ]),
    nicePageName () {
      let strName = "";
      this.page.name.split("-").forEach(x => {
        if (strName === "" && x !== "") { // capitalise first letter
          strName = x[0].toUpperCase();
          strName = strName + x.slice(1);
        } else {
          strName = strName + " " + x;
        }
      });
      return strName;
    }
  },
  methods: {
    getIconColor (color: number) {
      switch (color) {
        case 2:
          return "#fff";
        default:
          return "#000";
      }
    },
    goHome () {
      this.$router.push("/");
    }
  }
});
